.header {
	height: 95vh;
	background-image:
		linear-gradient(
			to bottom right ,
			rgba($color-green-light, 0.8),
			rgba($color-green-medium, 0.8)
		),
		url(../../img/hero-small.jpg);

	@media (min-resolution: 192dpi) and (min-width: $breakpoint-phone), (min-width: 125em) {
		background-image:
		linear-gradient(
			to bottom right ,
			rgba($color-green-light, 0.8),
			rgba($color-green-dark, 0.8)
		),
		url(../../img/hero.jpg);
	}
	background-size: cover;
	clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
	position: relative;
	@include respond(tab-port) {
		height: 85vh;
		clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
	}
	@include respond(phone) {
		height: 75vh;
		clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
	}

	&__logo {
		height: 3.5rem;

		&-box {
			position: absolute;
			top: 4rem;
			left: 4rem;
		}
	}

	&__box-text {
		position: absolute;
		left: 50%;
		top: 40%;
		transform: translate(-50%, -50%);
		text-align: center;
	}
}
